import headerCSS from './Header.module.css';
import HeadersLinks from './headersLinks/HeadersLinks';
const Header = () => {
    return (
        <header className={headerCSS.header}>
            <div className={headerCSS.logo}>Skilcum</div>
            <div className={headerCSS.allCoursesButton}>Все курсы</div>
            <HeadersLinks/>
            <div className={headerCSS.loginButton}>Войти</div>
        </header>
    )
}

export default Header;