import BBCSS from './ButtonBlocks.module.css'

const ButtonBlocks = (props) => {
    return (
        <div className={BBCSS.ButtonBlocksWrapper}>
            <p>Наши направления:</p>
            <div className={BBCSS.ButtonsBlock}>
            {props.children}
            </div>
        </div>
    )
}

export default ButtonBlocks;