import linkCSS from './HeadersLInks.module.css'

const HeadersLinks = (props) => {
    return(
        <div className={linkCSS.HeadersLinks}>
            <HeadLink url="#" text="О SkillCum"/>
            <HeadLink url="#" text="Вебинары"/>
            <HeadLink url="#" text="Журнал"/>
        </div>
    )
}


const HeadLink = (props) => {
    return (
        <a href={props.url} className={linkCSS.HeadLink}>{props.text}</a>
    );
}

export default HeadersLinks;