import React from 'react';
import WarnCSS from './WarningFullScreen.module.css';
class WarningFullScreen extends React.Component {
    constructor(props) {
        super(props);
        this.state = {showAlert: true};
    }

    hideAlert (e) {
        this.setState({showAlert: false});
    }
    render() {
        return (
            
            this.state.showAlert && <div className={WarnCSS.wrapper}>
                <div className={WarnCSS.warning}>
                    <h2>Отказ от ответственности</h2>
                    <p>Нажимая кнопку <b>продолжить</b> вы соглашаетесь со следующим:</p>
                    <ul>
                        <li>Вам уже есть 18 лет</li>
                        <li>Данный сайт носит исключительно пародийные и развлекатльные цели</li>
                        <li>Любые совпадения с реальными личностями, компаниями и явлениями - случайны</li>
                        <li>Мы не ставили цели кого-то оскорбить или обидеть</li>
                    </ul>
                    <button onClick={this.hideAlert.bind(this)}>Продолжить</button>
                </div>
            </div>
        );
    }
}

export default WarningFullScreen;