import PBCSS from './CompanyBar.module.css'

const CompanyBarElement = (props) => {
    return (
        <a id={"ProffisonBarBlock" + props.id} href={props.link} className={PBCSS.ProfessionBarElementWrapper + " " + PBCSS["ProffisonBarBlock" + props.id]}>
            <div className={PBCSS.ProfessionBarElement}>
                    <div className={PBCSS.name}>{props.name}</div>
            </div>
        </a>
    )
}

export default CompanyBarElement;