import tikTokLogo from '../../../Assets/ProffessionBar/tik_tok_logo.png'
const CoursesBar = (props) => {
    return (
        <div className={PBCSS.ProfessionBarWrapper}>
            <h2>Профессии с трудоустройством</h2>
            <p>
            Большие образовательные программы, чтобы освоить новую специальность с самого нуля. Во время обучения <b>мы поможем вам найти работу</b>.
            </p>
            <div className={PBCSS.ProfessionBar}>
                <ProfessionBarElement 
                    typeTitle="профессия"
                    name="Вебкам шлюха"
                    timeLerningTime="12"
                    timeLerningLabel="дней"
                    link="#"
                    icon={iconGirlWebcam}
                    id="1"
                />
                 <ProfessionBarElement 
                    typeTitle="профессия"
                    name="OnlyFuns шлюха"
                    timeLerningTime="6"
                    timeLerningLabel="дней"
                    link="#"
                    icon={iconGirlOnlyFuns}
                    id="2"
                />
                <ProfessionBarElement 
                    typeTitle="профессия"
                    name="Стримерка шлюха"
                    timeLerningTime="28"
                    timeLerningLabel="дней"
                    link="#"
                    icon={iconGirlStrimmer}
                    id="3"
                />
                <ProfessionBarElement 
                    typeTitle="профессия"
                    name="Инстаграм шлюха"
                    timeLerningTime="28"
                    timeLerningLabel="дней"
                    link="#"
                    icon={iconGirlInsta}
                    id="4"
                />
                <ProfessionBarElement 
                    typeTitle="профессия"
                    name="PornHub шлюха"
                    timeLerningTime="28"
                    timeLerningLabel="дней"
                    link="#"
                    icon={iconGirPornHub}
                    id="5"
                />
                <ProfessionBarElement 
                    typeTitle="профессия"
                    name="tikTok шлюха"
                    timeLerningTime="15"
                    timeLerningLabel="дней"
                    link="#"
                    icon={tikTokLogo}
                    id="6"
                />
            </div>
        </div>
    )
}

export default CoursesBar;