import HighEduCSS from './HighEduBlock.module.css'
import vuzGirlImg from '../../../Assets/ProffessionBar/girl_vuz.png'
const HighEduBlock = (props) => {
    return (
        <div className={HighEduCSS.Wpapper}>
            <div className={HighEduCSS.LeftSide}>
                <h1>Высшее образование в SkillCum</h1>
                <ul>
                    <li>Программы бакалавриата и магистратуры</li>
                    <li>Совместно с ведущими вузами</li>
                    <li>диплом государственного образца</li>
                </ul>
                <div className={HighEduCSS.button}>Перейти к программам</div>
            </div>
            <div className={HighEduCSS.rightSide}>
                <div className={HighEduCSS.vuzTitle}>НИУ ВШЭ</div>
                <div className={HighEduCSS.vuzGirl} style={{"background-image": "url(" + vuzGirlImg +")"}}></div>
            </div>
            
        </div>
    )
}

export default HighEduBlock;