import PBCSS from './CoursesBar.module.css'

const CoursesBarElement = (props) => {
    return (
        <a id={"ProffisonBarBlock" + props.id} href={props.link} className={PBCSS.ProfessionBarElementWrapper + " " + PBCSS["ProffisonBarBlock" + props.id]}>
            <div className={PBCSS.ProfessionBarElement}>
                <div className={PBCSS.leftSide}>
                    <div className={PBCSS.typeTitle}>{props.typeTitle}</div>
                    <div className={PBCSS.name}>{props.name}</div>
                    <div className={PBCSS.timeLerning}><b>{props.timeLerningTime}</b> {props.timeLerningLabel}</div>
                </div>
                <div className={PBCSS.rightSide}>
                    <div className={PBCSS.icon} style={{"background-image" : "url(" + props.icon + ")"}}></div>
                </div>
            </div>

        </a>
    )
}

export default CoursesBarElement;