import PBCSS from './CoursesBar.module.css'
import CoursesBar from "./CoursesBarElement" 
import iconGirlWebcam from '../../../Assets/ProffessionBar/girl_webcam.svg'
import iconGirlOnlyFuns from '../../../Assets/ProffessionBar/girl_onlyfuns.png'
import iconGirlStrimmer from '../../../Assets/ProffessionBar/girl_strimmer.png'
import iconGirlInsta from '../../../Assets/ProffessionBar/girl_insta.png'
import iconGirPornHub from '../../../Assets/ProffessionBar/girl_pornhub.png'
import tikTokLogo from '../../../Assets/ProffessionBar/tik_tok_logo.png'
const ProfessionBar = (props) => {
    return (
        <div className={PBCSS.ProfessionBarWrapper}>
            <h2>Курсы</h2>
            <p>
            Короткие программы, чтобы изучить один конкретный навык.
            </p>
            <div className={PBCSS.ProfessionBar}>
                <CoursesBar 
                    typeTitle="курс"
                    name="Ретушь жопы"
                    timeLerningTime="14"
                    timeLerningLabel="дней"
                    link="#"
                    icon={iconGirlWebcam}
                    id="1"
                />
                 <CoursesBar 
                    typeTitle="курс"
                    name="Правильные ракурсы"
                    timeLerningTime="6"
                    timeLerningLabel="дней"
                    link="#"
                    icon={iconGirlOnlyFuns}
                    id="2"
                />
                <CoursesBar 
                    typeTitle="курс"
                    name="Покупка диладков"
                    timeLerningTime="5"
                    timeLerningLabel="дней"
                    link="#"
                    icon={iconGirlStrimmer}
                    id="3"
                />
                <CoursesBar 
                    typeTitle="курс"
                    name="Оборудование стриммера"
                    timeLerningTime="14"
                    timeLerningLabel="дней"
                    link="#"
                    icon={iconGirlInsta}
                    id="4"
                />
                <CoursesBar 
                    typeTitle="курс"
                    name="Лучшие позы"
                    timeLerningTime="12"
                    timeLerningLabel="дней"
                    link="#"
                    icon={iconGirPornHub}
                    id="5"
                />
                <CoursesBar 
                    typeTitle="курс"
                    name="Раскрутка тиктока"
                    timeLerningTime="15"
                    timeLerningLabel="дней"
                    link="#"
                    icon={tikTokLogo}
                    id="6"
                />
            </div>
            <div className={PBCSS.button}>Показать еще 156 курсов</div>
        </div>
    )
}

export default ProfessionBar;